

function Prompt () {
    return (
        <div className='container d-flex row gx-4'>
            <div className='col row'>
                <input className='input-prompt border-curve-10' type='text'></input>
            </div>
            <div className='col-1'>
                <button className='btn btn-primary border-curve-10'>^</button>
            </div>
        </div>
    )
}

export default Prompt